html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  overflow-x: hidden;
  line-height: 2;
}

h1,
h2,
h3 {
  margin: 1rem;
  line-height: 1.6;
}

h1,
h2 {
  font-family: "Arima Madurai", cursive;
  font-size: 7rem;
}

h3,
p,
a {
  font-family: "Mulish", sans-serif;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
  margin: 1rem;
}

a {
  text-decoration: none;
  line-height: 1.6;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  width: 100%;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.verde {
  color: #00a149;
}

.rojo {
  color: #881014;
}

.ocultar {
  display: none;
}

.btn {
  color: #fff;
  font-size: 1.8rem;
  text-transform: capitalize;
  padding: 1rem 5rem;
  cursor: pointer;
  transition: 0.2s all ease-in-out;
}
@media (min-width: 700px) {
  .btn {
    font-size: 2.2rem;
  }
}

.btn-rojo {
  display: inline-block;
  margin-top: 2rem;
  margin-bottom: 2rem;
  background-color: #881014;
  font-weight: bold;
}
.btn-rojo:hover {
  background-color: #fff;
  border: 2px solid #00a149;
  color: #00a149;
}

.btn-verde {
  background-color: #00a149;
  border: none;
  display: inline-block;
  margin-bottom: 2rem;
  font-family: "Mulish", sans-serif;
}
.btn-verde:hover {
  background-color: #881014;
}

.bordes {
  border: 3px solid #fff;
  border-radius: 5px;
}
.bordes:hover {
  color: #00a149;
  background-color: #fff;
}
@media (min-width: 700px) {
  .bordes {
    border: 4px solid #fff;
  }
}

.btnClose {
  position: absolute;
  top: 0;
  right: 5rem;
  font-size: 3rem;
  font-weight: bold;
  user-select: none;
  color: #fff;
}
.btnClose:hover {
  cursor: pointer;
}

.full-screen {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}

@keyframes desplazar {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
.header__nav {
  background-color: #111;
  position: relative;
  z-index: 2;
}

.nav__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.nav__content .logo h2,
.nav__content .logo a,
.nav__content .nav h2,
.nav__content .nav a {
  color: #fff;
}
.nav__content .logo h2 {
  font-size: 3rem;
}
.nav__content .hamburguer span {
  display: block;
  background-color: #fff;
  width: 5rem;
  height: 0.5rem;
  margin: 1rem 0;
}
.nav__content .hamburguer:hover {
  cursor: pointer;
}

.nav {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  padding: 3rem 4rem;
  background-color: #111;
  animation: desplazar 0.5s ease-in-out;
}
@media (min-width: 700px) {
  .nav {
    width: 40rem;
  }
}
.nav a {
  display: block;
  padding: 1.5rem;
  padding-left: 4rem;
  font-size: 2.2rem;
  text-align: left;
  margin-bottom: 1.5rem;
  transition: 0.3s ease-in-out;
}
.nav a:first-of-type {
  margin-top: 5rem;
}
.nav a:nth-child(even):hover {
  color: #881014;
}
.nav a:nth-child(odd):hover {
  color: #00a149;
}

.header__content {
  position: relative;
}
.header__content::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.header__content .header__text {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 80%;
  z-index: 1;
}
.header__content .header__text h2 {
  color: #fff;
  font-size: 4rem;
  margin-bottom: 1rem;
}
@media (min-width: 700px) {
  .header__content .header__text h2 {
    font-size: 7rem;
  }
}
.header__content video {
  width: 100%;
  height: 100%;
}

.about {
  padding-top: 4rem;
}
@media (min-width: 700px) {
  .about {
    display: flex;
    justify-content: space-between;
  }
}
.about__text, .about__img {
  flex: 0 0 calc(50% - 1rem);
}
.about__text .bienvenida {
  font-size: 4rem;
  color: #00a149;
  text-align: center;
}
.about__text h1 {
  font-size: 5rem;
  text-align: center;
}
@media (min-width: 700px) {
  .about__text .bienvenida,
.about__text h1 {
    text-align: left;
  }
}
.about__text p {
  font-size: 2rem;
}
.about__img {
  display: flex;
  justify-content: space-between;
}
.about__img .img-1,
.about__img .img-2 {
  flex: 0 0 calc(50% - 1rem);
}
.about__img .img-2 {
  margin-bottom: 1rem;
}
.about__img img {
  border-radius: 0.5rem;
}

.menu {
  padding-top: 4rem;
}
.menu__text {
  text-align: center;
  font-size: 4rem;
}
.menu .btn-platillos {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (min-width: 700px) {
  .menu .btn-platillos {
    display: inline-block;
  }
}
.menu .btn-platillos button {
  flex: 0 0 calc(50% - 1rem);
  padding: 1rem 3rem;
  cursor: pointer;
}
@media (min-width: 700px) {
  .menu .btn-platillos button {
    flex: unset;
  }
}
@media (min-width: 700px) {
  .menu .platillos {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.platillo {
  flex: 0 0 calc(33.3% - 1rem);
  border: 1px solid #e1e1;
  padding: 1.5rem;
  border-radius: 5px;
}
.platillo img {
  height: 25rem;
}
.platillo h3 {
  text-align: center;
}
.platillo p {
  font-size: 1.6rem;
}
.platillo .price {
  display: flex;
  justify-content: space-between;
}
.platillo .price p {
  font-weight: bold;
  font-size: 2rem;
}
.platillo .price button {
  padding: 1rem;
  border-radius: 5px;
  border: none;
  background-color: #881014;
  color: #fff;
  height: 3.8rem;
  cursor: pointer;
}

.separator {
  position: relative;
  background-image: url(../assets/img/ensalada.webp);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  z-index: 1;
}
.separator::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: -1;
}
.separator__content {
  padding: 5rem;
}
.separator__content h2 {
  font-size: 3.2rem;
  color: #fff;
}
.separator__content p {
  color: #fff;
  font-size: 1.8rem;
}

/* CHEF */
@media (min-width: 700px) {
  .chef__content {
    display: flex;
  }
}
.chef h2 {
  display: inline-block;
  padding: 1rem 3rem;
  background: #881014;
  color: #fff;
  font-size: 3.2rem;
}
@media (min-width: 700px) {
  .chef__text, .chef__img {
    flex: 0 0 calc(50% - 1rem);
    justify-content: space-between;
  }
}
.chef__text h3 {
  font-size: 3rem;
}
.chef__text p {
  font-size: 2rem;
}

.contact {
  padding-top: 5rem;
}
@media (min-width: 700px) {
  .contact {
    display: flex;
  }
}
.contact__info,
.contact .form {
  flex: 0 0 calc(50% - 1rem);
}
.contact__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #111;
  padding: 5rem;
}
.contact__info h3,
.contact__info p,
.contact__info i {
  color: #fff;
}
.contact__info h3 {
  font-size: 4rem;
  font-family: "Arima Madurai", cursive;
}
.contact__info p {
  font-size: 1.8rem;
}
.contact__info i {
  margin-right: 1rem;
}
.contact__info .social {
  font-size: 3rem;
}

.form {
  padding: 5rem;
}
.form__input label {
  display: block;
  font-size: 2rem;
  font-family: "Mulish", sans-serif;
  margin-bottom: 1rem;
}
.form__input input,
.form__input textarea {
  width: 100%;
  padding: 1rem 1.6rem;
  font-family: "Mulish", sans-serif;
  border: 1px solid #e1e1;
  border-radius: 5px;
  outline: none;
}
.form__input textarea {
  height: 15rem;
  resize: none;
}

.foot {
  margin-top: 5rem;
  padding: 5rem;
  background: #141414;
}
@media (min-width: 700px) {
  .foot__content {
    display: flex;
    justify-content: space-between;
  }
}
.foot__info {
  flex: 0 0 calc(25% - 1rem);
  margin-bottom: 2rem;
}
.foot__info h3,
.foot__info p {
  text-align: center;
  color: #fff;
}
.foot__info h3 {
  font-size: 2.2rem;
}
.foot__info p {
  font-size: 1.8rem;
}
.foot__info input {
  width: 100%;
}
.foot__info input[type=email] {
  display: inline-block;
  margin-bottom: 1rem;
  padding: 1rem;
  border-radius: 5px;
  font-family: "Mulish", sans-serif;
  outline: none;
}
.foot__info input[type=submit] {
  padding: 1rem 2rem;
  font-size: 2rem;
}
.foot__social {
  text-align: center;
}

.footer {
  text-align: center;
  color: #fff;
  font-size: 2rem;
  padding: 2rem;
  background: #111;
}